import { INITIALIZE_FILTERS, SET_TEXT_FILTER, SORT_BY_PRIX, SORT_BY_SUPERFICIE ,
  SET_NB_PIECES, SET_GENRE_FILTER } from '../actions/filtersActions.js';

export default (state=filtersDefaultState, action)=>{
  switch(action.type){
    case 'SET_TEXT_FILTER':
    return {
      ...state,
      text: action.text
    };

    case 'SET_NB_PIECES':
    return {
      ...state,
      nbPieces: action.nbPieces
    };

    case 'SET_GENRE_FILTER':
    return{
      ...state,
      nbPieces:'studio 2 pi√®ces 3 pi√®ces 4 pi√®ces 5 pi√®ces',
      genre: action.genre
    };

  case 'SORT_BY_PRIX':
    return{
      ...state,
      sortBy: 'prix',
    };
  case 'SORT_BY_SUPERFICIE':
    return{
      ...state,
      sortBy: 'superficie',
    };
  case 'INITIALIZE_FILTERS':
    return{
      ...state,
      text: '',
      genre: 'AppartementMaisonCommerceTerrain',
      nbPieces: 'studio 2 pi√®ces 3 pi√®ces 4 pi√®ces 5 pi√®ces',
      sortBy: 'superficie',
    };
  default:  return state;
  };
}


const filtersDefaultState={
    text: '',
    genre: 'AppartementMaisonCommerceTerrain',
    nbPieces: 'studio 2 pi√®ces 3 pi√®ces 4 pi√®ces 5 pi√®ces',
    sortBy: 'superficie',
}



